/* Variables */

:root {
  --color-blue: #2360FE;
  --color-light-dark-blue: #1C1925;
  --color-dark-gray: #272237;
  --color-light-gray: #F0F6F7;
  --color-white: #FFFFFF;
  --color-dark-blue: #221828;
  --color-dark: #241B2A;
  --color-bg-primary: #1D1324;
  --color-accent-gradient: linear-gradient(90deg, #EB5733 0%, #E93873 100%);
  --color-accent-secondary: #E82F84;
  --font-helvetica-neue-cyr: "HelveticaNeueCyr", "Helvetica Neue", Helvetica, Arial, sans-serif;
}

/* Base */

* {
  box-sizing: border-box;
}

html {
  scroll-behavior: smooth;
}

html,
body {
  height: 100%;
}

img {
  max-width: 100%;
}

/* Animation */

@-webkit-keyframes wiggle-hand {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(15deg);
  }
}

@keyframes wiggle-hand {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(15deg);
  }
}

@-webkit-keyframes wiggle-papper {
  0% {
    transform: rotate(5deg);
  }

  100% {
    transform: rotate(10deg);
  }
}

@keyframes wiggle-papper {
  0% {
    transform: rotate(5deg);
  }

  100% {
    transform: rotate(10deg);
  }
}

@-webkit-keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@keyframes rotate {
  0% {
    transform: rotate(0deg);
  }

  100% {
    transform: rotate(360deg);
  }
}

@-webkit-keyframes flicker {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.85;
  }
}

@keyframes flicker {
  0% {
    opacity: 0;
  }

  100% {
    opacity: 0.85;
  }
}

@-webkit-keyframes scale-music {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.1);
  }
}

@keyframes scale-music {
  0% {
    transform: scale(1);
  }

  100% {
    transform: scale(1.1);
  }
}

@-webkit-keyframes move-eye {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-2px);
  }
}

@keyframes move-eye {
  0% {
    transform: translateX(0px);
  }

  100% {
    transform: translateX(-2px);
  }
}

@-webkit-keyframes change-color-monitor {
  0% {
    fill: #C8FCFF;
  }

  100% {
    fill: red;
  }
}

@keyframes change-color-monitor {
  0% {
    fill: #C8FCFF;
  }

  100% {
    fill: red;
  }
}

@-webkit-keyframes change-color-word {
  0% {
    fill: #596694;
  }

  100% {
    fill: #ffffff;
  }
}

@keyframes change-color-word {
  0% {
    fill: #596694;
  }

  100% {
    fill: #ffffff;
  }
}

@-webkit-keyframes flight-up {
  0% {
    transform: translateY(60px) rotate(0deg);
    opacity: 0;
  }

  50% {
    transform: translateY(30px) rotate(5deg);
    opacity: 1;
  }

  75% {
    transform: translateY(0px) rotate(-5deg);
    opacity: 0;
  }

  100% {
    transform: translateY(0px) rotate(-5deg);
    opacity: 0;
  }
}

@keyframes flight-up {
  0% {
    transform: translateY(60px) rotate(0deg);
    opacity: 0;
  }

  50% {
    transform: translateY(30px) rotate(5deg);
    opacity: 1;
  }

  75% {
    transform: translateY(0px) rotate(-5deg);
    opacity: 0;
  }

  100% {
    transform: translateY(0px) rotate(-5deg);
    opacity: 0;
  }
}

