/* Base Classes */

/* Containers */

.container {
  max-width: 1200px;
  margin: 0 auto;
  padding-left: 30px;
  padding-right: 30px;
}

.container-full {
  padding-left: 30px;
  padding-right: 30px;
}

/* Debug */

/* Hide */

.hide {
  display: none !important;
}

/* Position */

.left {
  text-align: left;
}

.center {
  text-align: center;
}

.right {
  text-align: right;
}

/* Background */

.bg--white {
  background-color: var(--color-white);
}

.bg--dark {
  background-color: var(--color-dark);
}

.bg--primary {
  background-color: var(--color-bg-primary);
}

.bg--dark-blue {
  background-color: var(--color-dark-blue);
}

.block-padding {
  padding: 75px 0;
}

/* Links */

.link-secondary {
  display: inline-block;
  font-size: calc(14px + 2 * ((100vw - 1024px) / 416));
  font-weight: 300;
  color: var(--color-blue);
  text-decoration: none;
  transition: color ease-out 0.15s, fill ease-out 0.15s;
  border: 1px solid transparent;
  padding: 4px;
  border-radius: 5px;
  display: flex;
  align-items: center;
}

@media screen and (min-width: 1440px) {

  .link-secondary {
    font-size: 16px;
  }
}

@media screen and (max-width: 1024px) {

  .link-secondary {
    font-size: 14px;
  }
}

.link-secondary__arrow-secondary {
  display: block;
  fill: var(--color-blue);
  transition: fill ease-out 0.15s, transform ease-out 0.15s;
  margin-left: 10px;
}

.link-secondary:hover,
.link-secondary:focus,
.link-secondary:active {
  color: var(--color-white);
}

.link-secondary:target,
.link-secondary:focus {
  border: 1px solid var(--color-blue);
}

.link-secondary:disabled {
  opacity: 0.45;
}

.link-secondary:hover .link-secondary__arrow-secondary,
.link-secondary:focus .link-secondary__arrow-secondary,
.link-secondary:active .link-secondary__arrow-secondary {
  fill: var(--color-white);
  transform: translateX(5px);
}

/* Title */

