/* ANOTHER MODULES */

/* ANOTHER MODULES */

/* Blocks */

/* Page */

.page {
  margin: 0;
  font-family: var(--font-helvetica-neue-cyr);
  font-size: 16px;
  line-height: 1.25;
  overflow-x: hidden;
  display: flex;
  flex-direction: column;
  align-items: stretch;
}

.page__container {
  width: 100%;
  margin: 0 auto;
  overflow: hidden;
  flex: 1 0 auto;
}

/* Scrollbar */

body::-webkit-scrollbar-track {
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  border-radius: 2px;
  background-color: rgba(18, 18, 18, 0.25);
}

body::-webkit-scrollbar {
  width: 4px;
  background-color: rgba(18, 18, 18, 0.25);
}

body::-webkit-scrollbar-thumb {
  border-radius: 2px;
  -webkit-box-shadow: inset 0 0 6px rgba(0, 0, 0, 0.3);
  background-color: #123548;
}

/* Buttons */

.button {
  font-family: var(--font-helvetica-neue-cyr);
  font-weight: 500;
  position: relative;
  cursor: pointer;
  display: inline-block;
  font-style: normal;
  text-decoration: none;
  text-align: center;
  font-size: 16px;
  line-height: 16px;
  border-radius: 5px;
  padding: 15px 35px;
  z-index: 10;
  box-shadow: 10px 10px 25px -10px rgba(0, 0, 0, 0.15);
  outline: var(--color-blue);
  transition: background 0.25s ease-in-out, color 0.25s ease-in-out, border 0.25s ease-in-out;
  overflow: hidden;
}

/* Bitton small */

.button--small {
  padding: 8px 15px;
  font-weight: 300;
}

/* Button with icon */

.button--icon {
  display: flex;
  align-items: center;
  justify-content: center;
}

.button__icon-white {
  display: inline-block;
  margin-left: 10px;
  fill: var(--color-white);
  transition: fill 0.3s ease-in-out, transform 0.3s ease-in-out;
}

.button--icon:hover .button__icon-white {
  fill: var(--color-accent-secondary);
  transform: translateX(5px);
}

/* Button dark */

.button--dark {
  border: 1px solid transparent;
  color: var(--color-light-gray);
  background: #241B2A;
  opacity: 0.55;
  transition: color 0.3s, background 0.3s, opacity 0.3s;
}

.button--dark:hover,
.button--dark:focus,
.button--dark:target,
.button--dark:active {
  opacity: 1;
  color: var(--color-accent-secondary);
}

.button--dark.is-active {
  opacity: 1;
  color: var(--color-white);
  background: var(--color-accent-gradient);
}

/* Button primary */

.button--primary {
  border: 1px solid transparent;
  color: var(--color-white);
  transition: color 0.3s, border 0.3s, background 0.3s;
}

.button--primary::before {
  content: "";
  position: absolute;
  height: 100%;
  width: 100%;
  top: 0;
  left: 0;
  background: var(--color-accent-gradient);
  z-index: -1;
  transition: width 0.25s;
}

.button--primary:hover::before,
.button--primary:focus::before,
.button--primary:target::before {
  width: 0%;
}

.button--primary:hover,
.button--primary:focus,
.button--primary:target {
  color: var(--color-accent-secondary);
  background: var(--color-white);
  border: 1px solid var(--color-white);
}

.button--primary:active {
  color: var(--color-accent-secondary);
  background: var(--color-white);
}

.button--primary:disabled {
  opacity: 0.45;
}

/* Button primary form */

.button--primary.button--form {
  background: var(--color-accent-gradient);
  border: 1px solid transparent;
  color: var(--color-white);
  transition: color 0.3s, border 0.3s, background 0.3s;
}

.button--primary.button--form:hover,
.button--primary.button--form:focus,
.button--primary.button--form:target {
  color: var(--color-accent-secondary);
  background: var(--color-white);
  border: 1px solid var(--color-white);
}

.button--primary.button--form:active {
  color: var(--color-accent-secondary);
  background: var(--color-white);
}

.button--primary.button--form:disabled {
  opacity: 0.45;
}

/* Button secondary */

.button--secondary {
  padding: 15px 35px;
  border: 1px solid var(--color-accent-secondary);
